.section.osc-request-info {
    background: $purple;
}
.section.osc-request-info .container {
    padding: 60px;
    -webkit-box-align: center;
    -webkit-align-items: center;
        -ms-flex-align: center;
            align-items: center;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.section.osc-request-info .osc {
    margin-right: 30px;
}
.section.osc-request-info .text {
    color: $white;
    font-family: 'Open Sans';
}
@media (min-width: $(tablet)) {
    .section.osc-request-info .text {
        -webkit-box-flex: 0;
        -webkit-flex: 0 0 auto;
            -ms-flex: 0 0 auto;
                flex: 0 0 auto;
        width: calc(99.99% * 8/12 - (30px - 30px * 8/12));
    }
    .section.osc-request-info .text:nth-child(1n) {
        margin-right: 30px;
        margin-left: 0;
    }
    .section.osc-request-info .text:last-child {
        margin-right: 0;
    }
    .section.osc-request-info .text:nth-child(12 '' ''n) {
        margin-right: 0;
        margin-left: auto;
    }
}
@media (min-width: $(desktop)) {
    .section.osc-request-info .text {
        -webkit-box-flex: 0;
        -webkit-flex: 0 0 auto;
            -ms-flex: 0 0 auto;
                flex: 0 0 auto;
        width: calc(99.99% * 5/12 - (30px - 30px * 5/12));
    }
    .section.osc-request-info .text:nth-child(1n) {
        margin-right: 30px;
        margin-left: 0;
    }
    .section.osc-request-info .text:last-child {
        margin-right: 0;
    }
    .section.osc-request-info .text:nth-child(12 '' ''n) {
        margin-right: 0;
        margin-left: auto;
    }
}
.section.osc-request-info .text a,
        .section.osc-request-info .text p {
    color: $white;
    font-family: 'Open Sans';
}
.section.osc-request-info .text .title h3 {
    font-size: 24px;
    color: $white;
    text-transform: none;
    margin-bottom: 10px;
}
.section.osc-request-info .text a {
    font-size: 18px;
    font-weight: bold;
    margin-top: 10px;
    display: block;
}
